<template>
  <el-card>
    <el-row type="flex">

      <el-col :span="10"
              :offset="1">
        <span>当前课程： </span>
        <el-select v-model="value"
                   @change="changeCourse"
                   placeholder="请选择">
          <el-option v-for="item in options"
                     :key="item.courseId"
                     :label="item.name"
                     :value="item.courseId">
          </el-option>
        </el-select>

      </el-col>
      <el-col :span="9">
        <div class="coursetime">选课学期：{{stuInfo.step           }} </div>

      </el-col>
      <el-col :span="7">
        <div class="coursetime">选课时间：{{stuInfo.addTime        }}</div>

      </el-col>
    </el-row>

    <el-divider></el-divider>

    <el-row type="flex">
      <el-col :span="6"
              :offset="1">
        <h1>
          课程信息
        </h1>
        <div class="middle">
          <div>课程类别：{{stuInfo.couKind        }}</div>
          <div>课程等级：{{stuInfo.xkLv           }}</div>
        </div>

      </el-col>
      <el-col :span="6">
        <h1>
          成绩信息
        </h1>
        <div class="middle">
          <div>成绩类型：{{stuInfo.examType       }} </div>
          <div>考试等级：{{stuInfo.examLv         }} </div>
        </div>

      </el-col>
      <el-col :span="6">
        <h1>
          获得学分
        </h1>
        <div class="score ">{{stuInfo.gotXf          }} 分</div>
      </el-col>
      <el-col :span="7">
        <h1>
          成绩分数
        </h1>

        <span>考试成绩(分数)
        </span>

        <el-progress :percentage="percentage"
                     :stroke-width="10"
                     :format="scpct"
                     :color="customColors"
                     class="progress"></el-progress>

        <span>形考成绩(分数)

        </span>

        <el-progress :percentage="xkpercentage"
                     :stroke-width="10"
                     :format="xkpct"
                     :color="customColors"
                     class="progress"></el-progress>
      </el-col>

    </el-row>

    <el-divider></el-divider>
    <el-row>
      <el-col :offset="1">
        <h1>
          学习信息
        </h1>
      </el-col>

    </el-row>

    <el-row type="flex">

      <el-col :offset="1">

        <el-row type="flex"
                class="middle">
          <el-col>
            <h4>讨论次数</h4>
            <div>
              <span class="score">{{stuInfo.discussCnt                }}</span> 次
            </div>
          </el-col>
          <el-col>
            <h4>学习时长</h4>
            <span class="score">{{stuInfo.learningDuration}}</span> 分钟
          </el-col>
          <el-col>
            <h4>登陆次数</h4>
            <span class="score">{{allCnt}}</span> 次
          </el-col>
        </el-row>

      </el-col>

      <el-col>
        <div id="runtime"></div>

      </el-col>

      <el-col>
        <div id="times"></div>
      </el-col>
    </el-row>

  </el-card>
</template>

<script>
export default {
  name: 'studyInfo',
  data () {
    return {
      //形考成绩百分比
      xkpercentage: 0,
      //考试成绩百分比
      percentage: 0,
      //进度条颜色
      customColors: [
        { color: '#909399', percentage: 40 },
        { color: '#f56c6c', percentage: 60 },
        { color: '#e6a23c', percentage: 70 },
        { color: '#67c23a', percentage: 90 },
        { color: '#1989fa', percentage: 100 }
      ],
      //课程列表
      options: [],
      //默认选中
      value: '',
      //学生id
      stuId: '',
      //默认课程id
      firstid: '',
      //课程名
      // courseName: '',
      //学生信息
      stuInfo: {
        step: '',
        examType: '',
        examLv: '',
        gotXf: '',
        xkScore: '',
        learningDuratio: '',
        appTimes: '',
        appRuntime: '',
        loginPreference: '',
        addTime: '',
        examScore: '',
        couKind: '',
        xkLv: '',
        discussCnt: '',
        pcTimes: '',
        pcRuntime: '',
        studyPreference: ''
      }
    }
  },
  computed: {
    allCnt () {
      return this.stuInfo.appTimes + this.stuInfo.pcTimes
    }
  },
  mounted () {
    var id = window.sessionStorage.getItem('storeData')
    this.stuId = id
    this.getCourseInfo(id)

  },
  created () {

  },
  methods: {
    //成绩图
    //形考成绩图
    xkpct (percentage) {
      return `${percentage}`;
    },
    //考试成绩图
    scpct (percentage) {
      return `${percentage}`;
    },
    //时长图
    getRunTime () {
      var myChart = this.$echarts.init(document.getElementById('runtime'));
      var pc = this.stuInfo.pcRuntime
      var app = this.stuInfo.appRuntime
      var per = this.stuInfo.studyPreference
      var option = {
        title: {
          text: '客户端学习时长',
          itemGap: 15,
          subtext: per,
          subtextStyle: {//副标题的属性
            fontSize: 15
          },
          x: 'center'
        },
        series: [
          {
            hoverAnimation: false,
            cursor: 'default',
            type: 'pie',
            radius: ['30%', "40%"], // 设置环形饼状图， 第一个百分数设置内圈大小，第二个百分数设置外圈大小
            center: ['50%', '50%'], // 设置饼状图位置，第一个百分数调水平位置，第二个百分数调垂直位置
            label: {
              normal: {
                formatter:
                  '{b}:{c}分钟' + '\n\r' + '({d}%)',
                // function (data) {
                //   return data.name + ":" + data.value + "分钟" + "\n" + " (" + data.percent.toFixed(0) + "%)";
                // },
                show: true,
                position: 'left'
              },
            },
            data: [
              { value: pc, name: 'PC端' },
              { value: app, name: 'App端' },
            ],
            itemStyle: {
              // normal: {
              //   borderColor: '#fff',
              //   borderWidth: '1'
              // },
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(128, 128, 128, 0.5)'
              }
            },
            // animation: false,
            color: ['#409EFF', '#ed8a8a']
          }
        ]
      }
      myChart.setOption(option);
    },
    //登陆次数
    getTimes () {
      var myChart = this.$echarts.init(document.getElementById('times'));
      var pc = this.stuInfo.pcTimes
      var app = this.stuInfo.appTimes
      var per = this.stuInfo.loginPreference
      var option = {
        title: {
          text: '客户端登陆次数',
          x: 'center',
          itemGap: 15,
          subtext: per,
          subtextStyle: {//副标题的属性
            fontSize: 15
          },

        },
        series: [
          {
            hoverAnimation: false,
            cursor: 'default',
            type: 'pie',
            radius: ['30%', "40%"], // 设置环形饼状图， 第一个百分数设置内圈大小，第二个百分数设置外圈大小
            center: ['50%', '50%'], // 设置饼状图位置，第一个百分数调水平位置，第二个百分数调垂直位置
            label: {
              normal: {
                formatter:
                  '{b}:{c}次' + '\n\r' + '({d}%)',
                show: true,
                position: 'left'
              },
            },
            data: [
              { value: pc, name: 'PC端' },
              { value: app, name: 'App端' },
            ],
            itemStyle: {
              // normal: {
              //   borderColor: '#fff',
              //   borderWidth: '1'
              // },
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(128, 128, 128, 0.5)'
              }
            },
            // animation: false,
            color: ['#409EFF', '#ed8a8a']
          }
        ]
      }
      myChart.setOption(option);
    },
    //形考成绩
    getxkScore () { },
    //考试成绩
    getScore () { },
    //改变下拉框
    changeCourse (value) {
      console.log(value);
      this.getStudyInfo(this.stuId, value)
    },
    // 获取课程名和课程id
    getCourseInfo (id) {
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/learnInfo/courseList',
        method: 'get',
        params: {
          stuId: id,
        }
      }).then(res => {
        console.log(res.data.data);
        this.options = res.data.data
        this.value = this.options[0].courseId
        this.firstid = this.options[0].courseId
        this.getStudyInfo(id, this.firstid)
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })

    },
    //获取学生学习情况
    getStudyInfo (id, courseId) {
      this.$ajax({
        url: 'http://119.29.243.108:8080/tbaps/learnInfo/baseInfo',
        method: 'get',
        params: {
          stuId: id,
          courseId: courseId
        }
      }).then(res => {
        console.log(res);
        this.stuInfo.step = res.data.data.step
        this.stuInfo.examType = res.data.data.examType
        this.stuInfo.examLv = res.data.data.examLv
        this.stuInfo.gotXf = res.data.data.gotXf
        this.stuInfo.xkScore = res.data.data.xkScore
        this.stuInfo.learningDuration = res.data.data.learningDuration
        this.stuInfo.appTimes = res.data.data.studyHabits.appTimes
        this.stuInfo.appRuntime = res.data.data.studyHabits.appRuntime
        this.stuInfo.loginPreference = res.data.data.studyHabits.loginPreference
        this.stuInfo.addTime = res.data.data.addTime
        this.stuInfo.examScore = res.data.data.examScore
        this.stuInfo.couKind = res.data.data.couKind
        this.stuInfo.xkLv = res.data.data.xkLv
        this.stuInfo.discussCnt = res.data.data.discussCnt
        this.stuInfo.pcTimes = res.data.data.studyHabits.pcTimes
        this.stuInfo.pcRuntime = res.data.data.studyHabits.pcRuntime
        this.stuInfo.studyPreference = res.data.data.studyHabits.studyPreference


        this.getRunTime()
        this.getTimes()
        this.xkpercentage = this.stuInfo.xkScore
        this.percentage = this.stuInfo.examScore
        // this.getScore()
        // this.getxkScore()
      }).catch(error => {
        this.$message.error('错误')
        console.log(error);
      })
    }
  },
  components: {

  }
}
</script>

<style>
#runtime {
  width: 400px;
  height: 220px;
}
#times {
  width: 400px;
  height: 220px;
}

.center {
  text-align: center;
}
.progress {
  width: 300px;
}
.score {
  font-size: 30px;
  /* text-align: center; */
  padding: 20px 0;
}
.coursetime {
  line-height: 40px;
}
.middle {
  /* line-height: 100%; */
  padding: 20px 0;
}
.center {
  text-align: center;
}
</style>





